//  - https://docs.cocos.com/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - https://docs.cocos.com/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

const { ccclass, property } = cc._decorator;

@ccclass
export default class Player extends cc.Component {
  ////////// 注入属性 //////////
  @property
  jumpHeight: number = 0; // 主角跳跃高度

  @property
  jumpDuration: number = 0; // 主角跳跃持续时间

  @property
  maxMoveSpeed: number = 0; // 最大移动速度

  @property
  accel: number = 0; // 加速度

  ////////// 默认属性 //////////
  accLeft: boolean = false; // 向左的加速度
  accRight: boolean = false; // 向右的加速度
  xSpeed: number = 0; // 主角当前水平方向的速度
  jumpAction: cc.ActionInterval = null; // 跳跃动作类

  ////////// 生命周期 //////////

  onLoad(): void {
    // 初始化跳跃动作
    this.jumpAction = this.setJumpAction();
    this.node.runAction(this.jumpAction);

    // 加速度方向开关
    this.accLeft = false;
    this.accRight = false;

    // 主角当前水平方向速度
    this.xSpeed = 0;

    // 初始化键盘键入监听
    cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
    cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);
  }

  start(): void {}

  update(dt: number) {
    // 根据当前加速度方向每帧更新速度
    if (this.accLeft) {
      this.xSpeed -= this.accel * dt;
    } else if (this.accRight) {
      this.xSpeed += this.accel * dt;
    }

    // 限制主角的速度不能超过最大值
    if (Math.abs(this.xSpeed) > this.maxMoveSpeed) {
      this.xSpeed = (this.maxMoveSpeed * this.xSpeed) / Math.abs(this.xSpeed);
    }

    // 根据当前速度更新主角的位置
    this.node.x += this.xSpeed * dt;
  }

  onDestroy(): void {
    // 取消键盘输入监听
    cc.systemEvent.off(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
    cc.systemEvent.off(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);
  }

  ////////// 功能函数 //////////

  /**
   * 设置跳跃
   */
  setJumpAction(): cc.ActionInterval {
    // 跳跃上升
    let jumpUp = cc
      .moveBy(this.jumpDuration, cc.v2(0, this.jumpHeight))
      .easing(cc.easeCubicActionOut());

    // 下落
    let jumpDown = cc
      .moveBy(this.jumpDuration, cc.v2(0, -this.jumpHeight))
      .easing(cc.easeCubicActionIn());

    // 不断重复
    return cc.repeatForever(cc.sequence(jumpUp, jumpDown));
  }

  /**
   * 按下键盘
   * @param {cc.Event.EventKeyboard} event 按键事件
   */
  onKeyDown(event: cc.Event.EventKeyboard): void {
    switch (event.keyCode) {
      case cc.macro.KEY.a:
        this.accLeft = true;
        break;
      case cc.macro.KEY.d:
        this.accRight = true;
        break;
    }
  }

  /**
   * 弹起键盘
   * @param {cc.Event.EventKeyboard} event 弹起的按键事件
   */
  onKeyUp(event: cc.Event.EventKeyboard): void {
    console.log(event);

    switch (event.keyCode) {
      case cc.macro.KEY.a:
        this.accLeft = false;
        break;
      case cc.macro.KEY.d:
        this.accRight = false;
        break;
    }
  }
}
